@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@200;300;400;500;600;700&display=swap);@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
*{padding:0;margin:0
}
ul{list-style:none
}
a{text-decoration:none;color:#fff;-webkit-transition:.5s;transition:.5s
}
a:hover{color:#ff1313
}
html,body{height:100%
}
body{background:#000;color:#fff;font-family:'Oswald', sans-serif
}
p{font-family:'Roboto', sans-serif
}
#app{width:1320px;margin:0 auto
}
#app #header{position:relative;height:60px
}
#app #header #top_logo{float:left
}
#app #header #top_logo a{height:60px;line-height:60px
}
#app #header #top_nav{position:relative;height:60px;float:right
}
#app #header #top_nav ul{float:left
}
#app #header #top_nav ul li{position:relative;float:left;margin-left:30px
}
#app #header #top_nav ul li:first-child{margin-left:0
}
#app #header #top_nav ul li a{display:inline-block;width:100%;height:100%;line-height:60px;height:60px
}
#app #header #top_nav ul li:hover .sub_menu{visibility:visible;top:100%;opacity:1
}
#app #header #top_nav ul li .sub_menu{position:absolute;border:1px solid #fff;visibility:hidden;top:calc( 100% - 3px);opacity:0;-webkit-transition:.5s;transition:.5s;z-index:500
}
#app #header #top_nav ul li .sub_menu li{width:100%;margin:0;background:white;-webkit-box-sizing:border-box;box-sizing:border-box;padding:0 10px;width:100%;min-width:150px;-webkit-transition:.5s;transition:.5s
}
#app #header #top_nav ul li .sub_menu li a{line-height:40px;height:40px;color:#000
}
#app #header #top_nav ul li .sub_menu li:hover{background:#ff1313
}
#app #header #top_nav ul li .sub_menu li:hover a{color:white
}
#app #footer{color:#fff;font-size:14px
}
#app #footer .footer_top{padding:60px 0 30px
}
#app #footer .container{margin:0 auto;width:100%
}
#app #footer .container .row{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-ms-flex-wrap:wrap;flex-wrap:wrap
}
#app #footer .container .row .column{-webkit-box-flex:25%;-ms-flex:25%;flex:25%;-webkit-box-sizing:border-box;box-sizing:border-box;padding:0 30px;margin-bottom:30px
}
#app #footer .container .row .column.footer_logo{-webkit-box-flex:100px;-ms-flex:100px;flex:100px
}
#app #footer .container .row .column.footer_logo h3{font-size:34px;margin:0 0 20px 0;padding:2px 0 2px 0px;line-height:1;font-weight:700
}
#app #footer .container .row .column.news p{margin-bottom:10px
}
#app #footer .container .row .column.news input[type=search]{border:0;padding:6px 8px;width:65%;outline:none
}
#app #footer .container .row .column.news input[type=submit]{background:#ff1313;border:0;width:35%;padding:6px 0;text-align:center;color:#ffffff;cursor:pointer
}
#app #footer .container .row .column h4{position:relative;font-size:14px;font-weight:700;color:#fff;text-transform:uppercase;padding-bottom:12px;margin-bottom:20px
}
#app #footer .container .row .column h4:before,#app #footer .container .row .column h4:after{content:'';position:absolute;left:0;bottom:0;height:3px
}
#app #footer .container .row .column h4:before{right:0;background:#ff1313
}
#app #footer .container .row .column h4:after{width:90px;background:#fff
}
#app #footer .container .row .column ul li{border-bottom:1px solid #fff;padding:10px 0
}
#app #footer .container .row .column ul li:nth-child(1){padding-top:0
}
#app #footer .container .row .column p{line-height:26px
}
#app #footer>.container{padding:30px 0;text-align:center
}
.box{display:-webkit-box;display:-ms-flexbox;display:flex
}
.box .items{-webkit-box-sizing:border-box;box-sizing:border-box;padding:50px;-webkit-box-flex:0;-ms-flex:0 0 100%;flex:0 0 100%;font-size:18px
}
.box .items .title{font-size:70px
}
.box .items .subtitle{text-align:center;font-size:70px;-webkit-text-stroke:1px #fff;-webkit-text-fill-color:transparent;text-transform:uppercase
}
.box .items p{margin:30px 0
}
.box .items img{width:100%
}
.box.items_2 .items{-webkit-box-flex:50%;-ms-flex:50%;flex:50%
}
.box.items_3{-webkit-box-flex:33.33%;-ms-flex:33.33%;flex:33.33%
}
.gbtn{display:inline-block;-webkit-box-sizing:border-box;box-sizing:border-box;padding:0 20px;height:60px;line-height:60px;text-align:center;background:#000;color:#fff;border:2px solid #fff;text-transform:uppercase;font-size:18px
}
.gbtn:hover{color:#fff;background:#ff1313
}
table{width:100%;table-layout:fixed
}
table+.gbtn{margin-top:20px;float:right
}
table tr th{padding:20px 15px;text-align:left;font-weight:500;font-size:16px;color:#ffffff;text-transform:uppercase
}
table tr th:nth-child(2){text-align:left
}
table tr td{padding:15px;text-align:left;vertical-align:middle;font-weight:300;font-size:16px;color:#fff;border-bottom:1px solid #fff
}
table tr td:nth-child(2){text-align:left
}
.tbl_header{margin-top:50px;background-color:#ff1313
}
.tbl_content{margin-top:0
}
.mainbanner{height:700px;background:url(/static/img/main_banner.6919bc3.jpg) no-repeat;background-size:cover
}
.mainbanner .row{position:relative;height:100%
}
.mainbanner .row .col{position:absolute;left:5%;top:25%
}
.mainbanner .row .col h1{font-size:100px;text-transform:uppercase
}
.mainbanner .row .col h1 span{-webkit-text-stroke:1px #fff;-webkit-text-fill-color:transparent
}
.inner{margin-top:50px
}
.inner.about .box.items_2 .items p{line-height:1.7;word-break:break-all
}
.inner.training .box:last-child .box.items_2{width:100%;text-align:center;padding:0 15px;-webkit-box-sizing:border-box;box-sizing:border-box
}
.inner.training .box:last-child .box.items_2 .items img{height:350px
}
.inner.training .box:last-child .box.items_2 .items h3{margin-top:10px;font-size:21px
}
.inner.training .box:last-child .box.items_2 .items p{font-size:16px;line-height:1.5;word-break:break-all
}
.inner.pricing .box .box.items_3{-webkit-box-shadow:0px 6px 6px 0px rgba(2,25,65,0.08);box-shadow:0px 6px 6px 0px rgba(2,25,65,0.08);border:1px solid rgba(255,255,255,0.2);padding:36px
}
.inner.pricing .box .box.items_3 .items img{width:100px;height:100px;margin-bottom:10px
}
.inner.pricing .box .box.items_3 .items p{position:relative;margin-left:25px
}
.inner.pricing .box .box.items_3 .items p:before{position:absolute;content:'';background:url(data:image/png;base64,);width:15px;height:15px;top:2.5px;left:-25px
}
.inner.pricing .box .box.items_3 .items a{width:100%
}
.inner.subbanner .box .items{padding:50px 0
}
.inner.subbanner .box:nth-child(2) .box.items_3{margin:0 30px;padding:0 30px
}
.inner.subbanner .box:nth-child(2) .box.items_3 .items{display:-webkit-box;display:-ms-flexbox;display:flex
}
.inner.subbanner .box:nth-child(2) .box.items_3 .items .icon{width:80px;height:80px;margin-right:30px;vertical-align:middle
}
main.subpage .container{display:-webkit-box;display:-ms-flexbox;display:flex
}
main.subpage .container #lnb{-webkit-box-flex:230px;-ms-flex:230px;flex:230px;-ms-flex-negative:0;flex-shrink:0;-webkit-box-flex:0;-ms-flex-positive:0;flex-grow:0;-webkit-box-sizing:border-box;box-sizing:border-box;padding-right:50px
}
main.subpage .container #lnb ul li{border-bottom:1px solid #fff
}
main.subpage .container #lnb ul li.on a{color:#fff;background:#ff1313;padding-left:20px
}
main.subpage .container #lnb ul li a{display:inline-block;width:100%;height:100%;line-height:50px;font-size:18px;-webkit-box-sizing:border-box;box-sizing:border-box;-webkit-transition:0.5s;transition:0.5s
}
main.subpage .container #lnb ul li a:hover{padding-left:20px
}
main.subpage .container .content{width:100%
}
main.subpage .container .content .sub_title span{position:relative;display:inline-block;padding-bottom:8px;line-height:43px
}
main.subpage .container .content .sub_title span:after{content:'';position:absolute;height:3px;background:#fff;bottom:0;left:0;right:0
}
main.subpage .container .content .inner+.inner{margin-top:50px;padding-top:50px;border-top:1px solid #fff
}
main.subpage .container .content .inner .box .items{padding:50px 0
}
main.subpage .container .content .inner.about .box .items{padding:50px
}
main.subpage .container .content .inner.training .box:last-child .box.items_2 .items p{line-height:1.5
}
main.subpage .container .content .inner.pricing .box:nth-child(2) .box.items_3{-webkit-box-sizing:border-box;box-sizing:border-box
}
main.subpage .container .content .inner.pricing .box:nth-child(3) .box.items_2{width:100%;-webkit-box-shadow:0px 6px 6px 0px rgba(2,25,65,0.08);box-shadow:0px 6px 6px 0px rgba(2,25,65,0.08);border:1px solid rgba(255,255,255,0.2);padding:40px
}
main.subpage .container .content .inner.pricing .box:nth-child(3) .box.items_2 .items img{width:100px;height:100px;margin-bottom:10px
}
main.subpage .container .content .inner.pricing .box:nth-child(3) .box.items_2 .items p{position:relative;margin-left:25px
}
main.subpage .container .content .inner.pricing .box:nth-child(3) .box.items_2 .items p:before{position:absolute;content:'';background:url(data:image/png;base64,);width:15px;height:15px;top:2.5px;left:-25px
}
main.subpage .container .content .inner.pricing .box:nth-child(3) .box.items_2 .items a{width:100%
}
main.subpage .container .content .inner.blog .box .box.items_2 h3{font-size:21px;margin-top:15px
}
main.subpage .container .content .inner.blog .box .box.items_2 h5{font-size:18px;display:inline-block;margin-top:15px;padding-bottom:5px;border-bottom:1px solid #ff1313
}
main.subpage .container .content .inner.blog .box .box.items_2 p{font-size:16px;line-height:1.4;word-break:break-all
}
main.subpage .container .content .inner.blog .box:nth-child(2) .box.items_2{-webkit-box-flex:100%;-ms-flex:100%;flex:100%;padding:0 30px
}
main.subpage .container .content .inner.blog .box:nth-child(3) .box.items_2{-webkit-box-flex:100%;-ms-flex:100%;flex:100%;padding:0 30px
}
main.subpage .container .content .inner.course .box{-ms-flex-wrap:wrap;flex-wrap:wrap
}
main.subpage .container .content .inner.course .box .items h4{font-size:32px;margin-top:15px
}
main.subpage .container .content .inner.gallery .box .box.items_3 .items{padding:0
}
main.subpage .container .content .inner.gallery .box .box.items_3 .items img{height:250px;-webkit-transition:.5s;transition:.5s
}
main.subpage .container .content .inner.gallery .box .box.items_3 .items img:hover{opacity:.8
}
main.subpage .container .content .inner.subbanner .box:nth-child(2) .box.items_3{padding:0
}
main.subpage .container .content .inner.subbanner .box:nth-child(2) .box.items_3 .items{padding:50px 20px
}
main.subpage .container .content .inner.subbanner .box:nth-child(2) .box.items_3 .items .icon{float:left;width:100px;height:100px;margin-right:20px
}
main.subpage .container .content .inner.faq tbody tr td:nth-child(2){cursor:pointer
}
